@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.site-nav {
  height: 60px;
  background-color: rgba(255, 255, 255, 0.733);
  box-shadow: var(--light-shadow);
  position: fixed;
  width:100%;
  top:0;
    z-index: 2;
}

.nav-menu {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 60px;
}

.nav-container {
  max-width: 1280px;
  padding: 0 24px;
  margin: 0 auto;
}

.top-link {
  font-size: 0.8rem;

  line-height: 20px;
  color: var(--grey1);
  padding-left: 12px;
  padding-right: 12px;
}
.active {
  color: var(--pink);
  font-weight: var(--ff-bold);
}

.footer {
  /* background: var(--white3); */
 background-color: rgba(255, 255, 255, 0.733);
  height:48px;
  position: fixed;
  bottom:0;
  width:100%;
 
    border-top: 1px solid #EBEBF2;
    font-size: 12px;
    color: #7D7A9E;
    
   
    padding: 0 25px;
   
    left: 0;
    z-index: 3;

    display:-webkit-flex;

    display:flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.social-icon{
  margin-left: 10px;
    vertical-align: middle;
    opacity: 0.6;
    cursor: pointer;
    transition: opacity 0.3s ease-in-out;
    padding: 10px;
}

.main {
  
  min-height: 380px;
  padding: 24px;
 
}

.main-container {
  max-width: 1280px;
  min-height: 380px;
  padding: 24px;
  margin:0 auto;
}
.banner-height {
  
  background: var(--purple6);
  margin-top: 60px;
}
.banner-container{
  display:grid;
  grid-template-columns: 1fr 500px;
  
  -webkit-align-items: center;
  
          align-items: center;
  max-width: 1180px;
  margin:0 auto;
  padding:40px;

}

.img-border{
  border: 12px solid #ffffff;
  height:520px;
  width:440px;
  
}
.left-side{
  max-width: 420px;
}
.right-side{
  margin: 0 auto;
}
.small-discription{
  
  color:var(--clr-grey-5);
  padding-bottom:20px;
}

.thumb-container {
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fit, minmax(290px, 390px));
  
  grid-column-gap: 30px;
  
  -webkit-column-gap: 30px;
  
          column-gap: 30px;
  grid-row-gap: 30px;
  row-gap: 30px;
  margin-top: 48px;
  position: relative;
  
}

.animited-box{
background:rgba(255,255,255,.4);
border-radius: 6px;
top:20%;
left:50%;
 -webkit-transform: translate(-50%,-20%);
         transform: translate(-50%,-20%);
 width: 140px;
  height: 100px;
  position: absolute;
  transition: var(--transition-ease); 
  opacity: 0;
}

  .animation .animited-box{
  width: 88%;
  height: 160px;
  opacity: 1;
}

.view-btn{
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);

}
.thumb-image {
  height: 200px;
}
.thumb-info {
  /* height: 60px; */
  padding: 8px 16px;
  line-height: 1.4;
  box-shadow: var(--light-shadow);
}

.txt-small {
  font-size: 0.74rem;
  color:var(--clr-grey-4)
}
.txt-lable {
  font-weight: var(--ff-bold);
}

@media (max-width:980px){
  .thumb-container{
-webkit-justify-content: center;
        justify-content: center;
  }

}
.border {
  border: 1px solid var(--orange4);
}

.bg-color {
  background: var(--white3);
}

.bg-grey {
  background: var(--white3);
}
.service-bg {
  background: #ebeef7;
  padding: 48px 0;
}
.three-column {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 280px));
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 20px;
  margin-top: 40px;
}
.item {
  text-align: center;
  background: rgba(255, 255, 255, 0.4);
  box-shadow: var(--light-shadow);
  border-radius: 3px;
  padding: 8px;
}

.contact-bg {
}

.experiance-bg {
}

.parallax {
  background-image: url(https://sarabsingh24.github.io/sarabportfolio/static/media/bg-animated.031ce77a.webp);

  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.bg-trans {
  /* background: rgba(255,255,255,.6); */
  background: rgb(186, 129, 255);
  background: linear-gradient(
    153deg,
    rgba(186, 129, 255, 0.7) 32%,
    rgba(255, 106, 149, 0.7) 100%
  );
}

.pop-bg{
    position: fixed;
    top:0;
    left:0;
    background: rgba(0,0,0,.6);
    width:100%;
    height:100%;
    z-index: 8;
}
.preview-header{
background: #fafafa;
    border-bottom: 1px solid #e1e8ed;
    padding-top: 8px;
    color: #454545;
}

.preview-info-contener{
    max-width: 1280px;
    
    padding: 24px;
    margin: 0 auto;
}
